<template>
  <div class="contact bg-light">
    <section class="webhero">
      <div class="container">
        <div class="row">
          <div class="col-md-12 text-center">
            <h1 class="display-1">Contact Us</h1>
            <p>Interested in our IT services or need advice? Then please get in touch and we’ll be glad to help.</p>
          </div>
        </div>
      </div>
    </section>
    <section class="py-5">
      <div class="container">
        <div class="row text-center gx-5">
          <div class="col-md-4">
            <div class="shadow p-3 rounded-5">
              <b-icon icon="envelope-open" font-scale="3"></b-icon>
              <h2>Get In Touch</h2>
              <div>Give us a call: (+91) 771 030 3298</div>
              <div>Opening Hours: 9 am – 6 pm  – 24/7</div>
              <div>Email us: info@itechvision.in</div>
            </div>
          </div>
          <div class="col-md-4">
            <div class="shadow p-3 rounded-5">
              <b-icon icon="geo-alt" font-scale="3"></b-icon>
              <h2>Fin Your Local</h2>
              <div>ItechVision delivers its advanced managed IT support services to clients worldwide.</div>
            </div>
          </div>
          <div class="col-md-4">
            <div class="shadow p-3 rounded-5">
              <b-icon icon="question-circle" font-scale="3"></b-icon>
              <h2>Have Questions</h2>
              <div>When it comes to managing IT for your business, you need an expert. That’s where we come in.</div>
            </div>
          </div>
        </div>
        <div class="row my-5 justify-content-center">
          <div class="col-md-7">
            <div class="contact-form shadow p-4 bg-white">
              <form ref="form" @submi.prevent="sendEmail">
                <div class="row gy-4">
                  <div class="col-md-6">
                    <label for="name" class="form-label">Full Name</label>
                    <input type="text" class="form-control" v-model="name" placeholder="eg:John Doe" required>
                  </div>
                  <div class="col-md-6">
                    <label for="company" class="form-label">Organization</label>
                    <input type="text" class="form-control" v-model="company" placeholder="Company Name" required>
                  </div>
                  <div class="col-md-6">
                    <label for="email" class="form-label">Work Email Address</label>
                    <input type="text" class="form-control" v-model="email" placeholder="name@company.com" required>
                  </div>
                  <div class="col-md-6">
                    <label for="phone" class="form-label">Phone No</label>
                    <input type="number" class="form-control" v-model="phone" placeholder="Phone No" required>
                  </div>
                  <div class="col-md-6">
                    <label for="comsize" class="form-label">Company Size</label>
                    <b-form-select v-model="selected1" :options="comsize" class="form-select"></b-form-select>
                  </div>
                  <div class="col-md-6">
                    <label for="services" class="form-label">What is your inquiry about?</label>
                    <b-form-select v-model="services" :options="services" class="form-select"></b-form-select>
                  </div>
                  <div class="col-md-12">
                    <label for="message" class="form-label">How we can help you? </label>
                    <textarea class="form-control" rows="5" placeholder="Let us Know what you need" required></textarea>
                  </div>
                  <button type="submit" class="btn btn-primary" value="send">Submit</button>

                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="text-center">You can also email us at <a href="mailto:Info@itechvision.in">Info@itechvision.in</a></div>
      </div>
    </section>
  </div>
</template>

<script>
import emailjs from '@emailjs/browser'
export default {
  name: "ContactView",
  metaInfo: {title: 'Contact Us',
    meta: [
      { name: 'description', content: 'Contact Us - we are available for all types of support regarding web and App Designing, Development, Testing and Deploy on Live Server etc.' },
      { property: 'og:title', content: 'Contact Us', template: chunk => `${chunk} -  Web Development Saas Company`,}
    ]
  },
  data(){
    return{
      name:'',
      company:'',
      email:'',
      phone:'',
      selected1:null,
      selected2:null,
      services: [
        { value: null, text: 'What is your IT Service Need? *' },
        { value: 'a', text: 'Managed IT Services' },
        { value: 'b', text: 'Cyber Security Services' },
        { value: 'c', text: 'IT Support Services' },
        { value: 'd', text: 'Cloud Computing' },
        { value: 'e', text: 'IT Consultancy' }
      ],
      comsize:[
          {value:null,text:'Please Select'},
          {value:1,text:'1(Freelancer)'},
          {value:2,text:'2-19'},
          {value:20,text:'20-49'},
          {value:50,text:'50-99'},
          {value:100,text:'100+'},
      ],
      message:'',
    }
  },
  // mounted() {
  //   this.sendEmail();
  // },
  methods: {
    // sendEmail(){
    //   emailjs.sendForm('service_ck0mn34', 'template_n47qx3p', this.$refs.form, 'on5nsBUZGKdDrQnDa')
    //       .then((result)=>{
    //         console.log('success',result.text);
    //       }, (error)=> {
    //         console.log('error', error.text);
    //   });
    // }
    sendEmail() {
      try {
        emailjs.sendForm('service_ck0mn34', 'template_n47qx3p', this.$refs.form, 'on5nsBUZGKdDrQnDa',
            {
              name: this.name,
              company: this.company,
              email: this.email,
              phone: this.phone,
              // services: this.services,
              // comsize: this.comsize,
              message: this.message
            })
      } catch (error) {
        console.log({error})
      }
      // Reset form field
      this.name = ''
      this.company = ''
      this.email = ''
      this.phone = ''
      // this.services = ''
      // this.comsize = ''
      this.message = ''
    }
  }
}
</script>

<style scoped>
.webhero {
  padding-bottom: 3%;
  background-image: url(@/assets/bg_footer.png);
}
</style>